import axios from 'axios';
import { ADD_POST, GET_ERRORS, GET_POSTS, POST_LOADING, DELETE_POST, GET_POST} from './types';

//Add Post

export const addPost = (postData) => (dispatch) => {
    axios.post('/api/posts', postData)
    .then(res => {
        console.log(res.data, "is this the post data");
        dispatch({
            type:ADD_POST,
            payload:res.data
        })
    }
    )
    .catch(err => 
        dispatch({
            type:GET_ERRORS,
            payload:err.response.data
        })
    );
    
}


//Get Posts

export const getPosts = () => (dispatch) => {
    dispatch(setPostLoading())
    axios.get('/api/posts')
    .then(res => {

        console.log(res);
        dispatch({
            type: GET_POSTS,
            payload: res.data
        })}
    )
    .catch(err => 
        dispatch({
            type:GET_POSTS,
            payload:null
        })
    );
    
}

//delete a Post
export const deletepost = (id) => (dispatch) => {
        axios.delete(`/api/posts/${id}`)
        .then(res => {
            dispatch({
                type:DELETE_POST,
                payload:res.data
            })
        })
        .catch(err => {

            dispatch({
                type:GET_ERRORS,
                payload:err.response.data
            })
        })
};

//Add a like to a post

export const addlike = (id) => (dispatch) => {
    axios.post(`/api/posts/like/${id}`)
    .then(res => {
       console.log(res, "has the like been added to this post")
       dispatch(getPosts());
    })
    .catch(err => {
        dispatch({
            type:GET_ERRORS,
            payload:err.response.data
        })
    })
}

//remove a like from a post

export const removelike = (Id) => (dispatch) => {
    axios.post(`/api/posts/unlike/${Id}`)
        .then(res => {
            dispatch(getPosts());
        })
        .catch((err => {
            dispatch({
                type: GET_ERRORS,
                payload:err.response.data
            })
        }))
}




//set loading State
export const setPostLoading = () => {
    return {
        type:POST_LOADING
    }
}

// get single post

export const getPost = (id) => (dispatch) => {
    dispatch(setPostLoading())
    axios.get(`/api/posts/${id}`)
    .then(res => {

        console.log(res);
        dispatch({
            type: GET_POST,
            payload: res.data
        })}
    )
    .catch(err => 
        dispatch({
            type:GET_POST,
            payload:null
        })
    );
    
}


//Add Comment

export const addComment = (postId, newComment) => (dispatch) => {
    axios.post(`/api/posts/comment/${postId}`, newComment)
    .then(res => {
        console.log(res.data, "is this the post data");
        dispatch(getPost(postId))
        })
    .catch(err => 
        dispatch({
            type:GET_ERRORS,
            payload:err.response.data
        })
    );
    
}

//Delete Comment

export const deleteComment = (postId, commentId) => (dispatch) => {
    axios.delete(`/api/posts/comment/${postId}/${commentId}`)
    .then(res => {
        dispatch(getPost(postId))
        })
    .catch(err => 
        dispatch({
            type:GET_ERRORS,
            payload:err.response.data
        })
    );
    
}